$nav-links-border-bottom: 4px;
.navbar {
	background: white;
 }	//margin-bottom: 1rem
.nav-item > a {
	font-family: $customFont; }
.second-level {
	//border: 1px solid blue !important
	padding: 0;
	margin: 0; }

nav ul li {
	.dropdown-menu {
		margin-top: 0;
		border-radius: 0;
		border: 0; }

	> a {
		font-size: 1.2rem;
		//text-transform: uppercase
		font-weight: 800; } }
.navbar-nav {
	li > a {
		padding: 1em 0 .5em 0; } }
.navbar-brand {
	padding: 0 1em 0 0;
	border-bottom: $nav-links-border-bottom solid transparent;
	svg {
		width: 7rem; } }

$doubleSection: false;

.navbar-nav {
	@if $doubleSection {
		li {
			a {
				border-bottom: $nav-links-border-bottom solid white; } }
		li:nth-child(1) {
			a.nav-link {
				color: $wdwBlue;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwBlue; } } }
		li:nth-child(2) {
			a.nav-link {
				color: $wdwBlue3;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwBlue3; } } }
		li:nth-child(3) {
			a.nav-link {
				color: $wdwPurple;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwPurple; } } }
		li:nth-child(4) {
			a.nav-link {
				color: $wdwPurple3;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwPurple3; } } }
		li:nth-child(5) {
			a.nav-link {
				color: $primaryColor;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $primaryColor; } } }
		li:nth-child(6) {
			a.nav-link {
				color: $wdwGreen;
				border-bottom: $nav-links-border-bottom solid white;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwGreen; } } } }
	@else {
		li:nth-child(1) {
			.dropdown-menu {
				border-top: 2px solid $wdwBlue;
				background:  $wdwBlue;
				h5 {
					color: lighten($wdwBlue, 30%); }
				a {
					color: white;
					font-size: 1em;
					&:hover {
						background: rgba(white, .1); } } }
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $wdwBlue;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwBlue; } } }
		li:nth-child(2) {
			.dropdown-menu {
				border-top: 2px solid $wdwPurple;
				background:  $wdwPurple;
				h5 {
					color: lighten($wdwPurple, 30%); }
				a {
					color: white;
					font-size: 1em;
					&:hover {
						background: rgba(white, .1); } } }
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $wdwPurple;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwPurple; } } }
		li:nth-child(3) {
			.dropdown-menu {
				border-top: 2px solid $primaryColor;
				background:  $primaryColor;
				h5 {
					color: lighten($primaryColor, 30%); }
				a {
					color: white;
					font-size: 1em;
					&:hover {
						background: rgba(white, .1); } } }
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $primaryColor;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $primaryColor; } } }
		li:nth-child(4) {
			.dropdown-menu {
				border-top: 2px solid $wdwOrange1;
				background:  $wdwOrange1;
				h5 {
					color: lighten($wdwOrange1, 30%); }
				a {
					color: white;
					font-size: 1em;
					&:hover {
						background: rgba(white, .1); } } }
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $wdwOrange1;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwOrange1; } } }
		li:nth-child(5) {
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $wdwGreen;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwGreen; } } } } }

.nav-item.dropdown-menu {
	h5, .list-unstyled a {

		font-family: sans-serif;
		font-weight: normal;
		font-size: 1em; } }


