.containerwdw {
    background: white;
    width: 100%;
    display: block;
    padding: 1em 0;
    &--sites {
        background: white; }
    &--links {
        background: $wdwBlack;
        padding: 0 1em; } }
.footer {
    &-links {
        flex-direction: column;
        &--list {
            //border: 1px solid red
            li a {
                margin-right: 1em; } } } }

.footer-links {
    &--list__social {
        border-bottom: 1px solid $wdwBlack3;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 1em;
        ul:nth-child(2) {
            //width: 100%
            display: block; }
        li:nth-last-child(1) {
            a {
                margin-right: 0; } } } }
.list-inline {
    &--svg {
        a {
            margin: 0 1em 1em 0; }
        svg {
            height: 30px; } } }

@media (max-width: 1100px) {
    .footer {
        &-cols {
            &--col {
                width: auto;
                padding: 0 1em;
                flex-grow: 1; } } } }
